import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { fetchMoreImages } from '../actions/searchAction';
import { fetchPage } from '../actions/searchAction';

import Dialog from 'material-ui/Dialog';
import IconButton from 'material-ui/IconButton';
import { Toolbar, ToolbarGroup, ToolbarSeparator, ToolbarTitle } from 'material-ui/Toolbar';

import OpenInNew from 'material-ui/svg-icons/action/open-in-new';
import NavigationChevronLeft from 'material-ui/svg-icons/navigation/chevron-left';
import NavigationChevronRight from 'material-ui/svg-icons/navigation/chevron-right';
import NavigationClose from 'material-ui/svg-icons/navigation/close';
import { grey100, grey300, grey400, grey800, deepOrange800, white } from 'material-ui/styles/colors';

import pixabayLogo2 from '../img/pixabayLogo2.svg';
// import PixabayLogo_1 from '../img/pixabayLogo_1';

class ImgViewer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      open: this.props.value.open,
      // currentImg: this.props.value.currentImg,
      currentImg: this.props.images[this.props.value.currentArrInd].largeImageURL,
      currentArrInd: this.props.value.currentArrInd,
      // currentImgPageUrl: this.props.value.currentImgPageUrl,
      currentImgPageUrl: this.props.images[this.props.value.currentArrInd].pageURL,
    };
    this.handleNextButton = this.handleNextButton.bind(this);
  }

  componentDidMount() {
    window.addEventListener('keydown', this.handleNextButton);
  }

  componentWillUnmount() {
    window.removeEventListener('keydown', this.handleNextButton);
  }

  handleImgOpen = (img, index) => {
    this.setState({
      open: true,
      currentImg: img.largeImageURL,
      currentArrInd: index,
      currentImgPageUrl: img.pageURL,
    });
  }

  handleImgClose = () => {
    this.props.onChange(false);
  }

  handleNextButton = (e = { keyCode: 0, key: '' }, side) => {
    let nextArrInd;
    console.log(e.keyCode, e.key, side);

    if ((side === 'right') || (e.key === 'ArrowRight') || (e.keyCode === 39)) {
      nextArrInd = this.state.currentArrInd + 1
    } else if ((side === 'left') || (e.key === 'ArrowLeft') || (e.keyCode === 37)) {
      nextArrInd = this.state.currentArrInd - 1
    } else return null

    // if ((nextArrInd > -1) && (nextArrInd < (this.props.value.images.length))) {
    //   this.handleImgOpen(this.props.value.images[nextArrInd], nextArrInd)
    // } else {
    //   this.handleImgClose()
    // }
    if (nextArrInd > -1) {
      switch(true) {
        case (nextArrInd < (this.props.images.length-1)):
          this.handleImgOpen(this.props.images[nextArrInd], nextArrInd) 
          console.log(nextArrInd);
          console.log(this.props.images.length);
          break;
        case (nextArrInd < this.props.images.length):
          console.log(nextArrInd);
          if (this.props.totalHits > this.props.images.length) {
            this.props.fetchPage(this.props.page + 1);
            this.props.fetchMoreImages();
            console.log(this.props.images.length);
            // this.handleImgOpen(this.props.images[nextArrInd], nextArrInd);
          } 
          this.handleImgOpen(this.props.images[nextArrInd], nextArrInd);
          break;
        case (nextArrInd === this.props.images.length):
          console.log(this.props.images.length);
            this.handleImgClose()
          break;
        default:
          break;
      }
    } else {
      this.handleImgClose()
    }
  }  

  render() {
    let imageViewer;

    // hook for correct paddingTop when opening Dialog with dynamic content; correct Dialog display for 'portrait'&'landskape':
    const dialogStyle = {
      dialogRoot: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        paddingTop: 0,
        width: '100%',
        // maxWidth: 'none',
      },
      dialogContent: {
        position: 'relative',
        transform: 'translate(0px, 0px)',
        width: '100%',
        maxWidth: '99vw',
      },
      dialogBody: {
        padding: 2,
        objectFit: 'contain',
      },
      // my additional settings
      dialogImg: {
        maxWidth: '80vw',
        maxHeight: '80vh',
      },
      dialogOverlay: {
        backgroundColor: 'rgba(0, 0, 0, 0.94)',
      },
    };

    const styles = {
      viewerNavigation: {
        position:'fixed',
        zIndex: 1501,
      },
      viewerIconButtonLR: {
        position: 'fixed',
        top: '35vh',
        // zIndex: 1501,
        // width: 'auto',
        width: '45vw',
        height: '30vh',
        padding: 3,
        // backgroundColor: 'rgba(0, 0, 0, 0.14)',
      },
      viewerIconButton: {
        position: 'fixed',
        maxHeight: '10vh',
        // top: 57,
        // right: 1,
        // zIndex: 1501,
        // width: 'auto',
        // width: '45vw',
        // height: '30vh',
        padding: 1,
        // backgroundColor: 'rgba(0, 0, 0, 0.14)',
      },
      viewerIconStyleLR: {
        width: '8vw',
        height: '8vw',
        maxHeight: 50,
        color: grey400,
      },
      viewerIconStyle: {
        // width: 54,
        // height: 'auto',
        color: grey400,
        float: 'right',
        height: '7vh', 
        width: '7vw',
        maxHeight: 30,
      },
      topToolBar: {
        position: 'fixed',
        top: 0,
        width: '100%',
        height: '9vh',
        // opacity: '0.8',
        backgroundColor: 'rgba(0, 0, 0, 0.5)',
      },
      imgCounter: {
        position: 'fixed',
        top: '2vh',
        left: '2vw',
        color: grey400,
        fontSize: 17,
      }
    }

    const actions = [
      // <FlatButton
      //   label='Left'
      //   primary={true}
      //   onClick={() => this.handleNextButton({}, 'left')}
      // />,
      // <FlatButton
      //   label='Right'
      //   primary={true}
      //   onClick={() => this.handleNextButton({}, 'right')}
      // />,
      // <FlatButton
      //   label = 'Close'
      //   primary = {true}
      //   onClick = {this.handleImgClose}
      // />
    ]

    // if (this.props.totalHits > this.props.images.length) {
    //   this.addScrollListener();
    //   // this.setState({
    //   //   showRefreshIndicator: true,
    //   // })
    // } else {
    //   console.log('totalHits < images.length')
    // }

    if (this.state.open) {
      imageViewer = (
        <div className='gridViewer'>
          <Dialog
            // actions={actions}
            modal={false}
            open={this.state.open}
            onRequestClose={this.handleImgClose}
            contentStyle={dialogStyle.dialogContent}
            bodyStyle={dialogStyle.dialogBody}
            style={dialogStyle.dialogRoot}
            repositionOnUpdate={false}
            autoDetectWindowHeight={false}
            overlayStyle={dialogStyle.dialogOverlay}
          >
            <img className='dialogIMG' src={this.state.currentImg} alt='' style={dialogStyle.dialogImg} />
            {/* <IconButton
              onClick={() => console.log(this.state.currentImgPageUrl)}
              style={styles.viewerIconButtonLink}
              iconStyle={styles.viewerIconStyle}
              // disableTouchRipple={true}
              href= {this.state.currentImgPageUrl}
            >
              <img src={pixabayLogo2} alt='link to details' />  
            </IconButton> */}

          </Dialog>
          <div className='viewerNavigation' style={styles.viewerNavigation}>
            <IconButton
              onClick={() => this.handleNextButton({}, 'left')}
              style={Object.assign({}, styles.viewerIconButtonLR, { left: 0 })}
              iconStyle={Object.assign({}, styles.viewerIconStyleLR, { float: 'left' })}
              disableTouchRipple={true}
            >
              <NavigationChevronLeft />
            </IconButton>
            <IconButton
              onClick={() => this.handleNextButton({}, 'right')}
              style={Object.assign({}, styles.viewerIconButtonLR, { right: 0 })}
              iconStyle={Object.assign({}, styles.viewerIconStyleLR, { float: 'right' })}
              disableTouchRipple={true}
            >
              <NavigationChevronRight />
            </IconButton>
            <Toolbar style={styles.topToolBar}>
              
              {/* <ToolbarGroup firstChild={true}>
                <ToolbarTitle 
                  text={this.props.totalHits}
                  style={styles.topToolBarTitle}
              />
              </ToolbarGroup> */}
              <ToolbarGroup lastChild={true} >
              </ToolbarGroup>  
            </Toolbar>
            <IconButton
              onClick={this.handleImgClose}
              style={Object.assign({}, styles.viewerIconButton, { top: 1, right: 3 })}
              iconStyle={Object.assign({}, styles.viewerIconStyle, {})}
              disableTouchRipple={true}
            >
              <NavigationClose />
            </IconButton>
            <div 
              className='imgCounter' 
              style={styles.imgCounter}> 
              {/* <div style={{paddingTop:'2vh'}}> */}
                {(this.state.currentArrInd + 1) + ' / ' + this.props.totalHits} 
              {/* </div> */}
            </div>
          </div>
          
          {/* <IconButton
            onClick={() => console.log(this.state.currentImgPageUrl)}
            style={styles.viewerIconButtonLink}
            iconStyle={styles.viewerIconStyle}
            // disableTouchRipple={true}
            href={this.state.currentImgPageUrl}
            target='_blank'
          > */}
            {/* <img src={pixabayLogo2} alt='link to details' /> */}
            {/* <PixabayLogo_1 /> */}
            {/* <svg>
              <use href={pixabayLogo2} />
            </svg> */}
          {/* </IconButton> */}
        </div>
      )
    } else {
      imageViewer = null
    }

    return (
      <div>
        {imageViewer}
      </div>
    )
  }
}

ImgViewer.propTypes = {
  fetchMoreImages: PropTypes.func,
  fetchPage: PropTypes.func,
  images: PropTypes.array,
  totalHits: PropTypes.number,
  page: PropTypes.number,
  // designType: PropTypes.number,
  // orientation: PropTypes.string,
  onChange: PropTypes.func.isRequired,
  value: PropTypes.object.isRequired,
}

const mapStateToProps = state => ({
  images: state.search.images,
  totalHits: state.search.total,
  page: state.search.page,
  // designType: state.search.designType,
  // orientation: state.search.orientation,
})

export default connect(mapStateToProps, { fetchMoreImages, fetchPage })(ImgViewer)


