import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { fetchImages } from '../../actions/searchAction';
import { fetchColor } from '../../actions/searchAction';

import SelectField from 'material-ui/SelectField';
import MenuItem from 'material-ui/MenuItem';
import Divider from 'material-ui/Divider';

const customStyle = {
  width: 200,
  margin: '0px 5px',
};

// Fix Divider from "marginLeft: 72" to 0
const dividerStyle = {
  root: {
    marginLeft: 0,
  }
};

class ImgColor extends Component {
  constructor(props) {
    super(props);
    // this.state = {
    //   value: this.props.value
    // }
    this.handleChange = this.handleChange.bind(this)
  }

  handleChange(e, index, val) {
    if (!(val.indexOf('reset') > -1)) {
      this.props.fetchColor(val)
    } else {
      this.props.fetchColor([])
    }
    this.props.fetchImages()
  }

  render() {
    const colorArray = ["grayscale", "transparent", "red", "orange", "yellow", "green",
    "turquoise", "blue", "lilac", "pink", "white", "gray", "black", "brown"];

    const booleanCheck = (x) => {
      if (this.props.color && (this.props.color.indexOf(x) > -1)) {
        return true
      } else {
        return false
      }
    } ;

    const MenuItemColor = (
      colorArray.map((val) => (
        <MenuItem
          key={val}
          value={val}
          primaryText={val}
          checked={booleanCheck(val)}
        />
      ))
    );

    return (
      <SelectField
        name='color'
        className='SelectField'
        // value={(this.props.value === '') ? 'reset' : this.props.value}
        value={this.props.color}
        onChange={this.handleChange}
        multiple={true}
        maxHeight={300}
        floatingLabelText='Colors'
        style={customStyle}
      >
        <MenuItem value='reset' primaryText='all' style={{ fontWeight: 'bold'}} />
        <Divider inset={true} style={dividerStyle.root}/>
        {MenuItemColor  }
      </SelectField>
    )
  }
}

ImgColor.propTypes = {
  fetchImages: PropTypes.func.isRequired,
  fetchColor: PropTypes.func.isRequired,
  color: PropTypes.array.isRequired,
}

const mapStateToProps = state => ({
  color: state.search.color,
})

export default connect(mapStateToProps, { fetchImages, fetchColor })(ImgColor)


